/* VARIABLES */
$primary-color: #e1a50f;
$grey-color: #b3b3b3;
$white-color: #ffffff;
$black-color: #000000;

/* GLOBAL STYLES */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    min-width: 240px;
    font-family: "Rubik", sans-serif;
    font-size: 14px;
    line-height: 1.5;
}

.container {
    width: 80%;
    max-width: 1280px;
    margin: auto;
    overflow: hidden;

    @media screen and (max-width: 768px) {
        width: 90%;
    }
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
    color: inherit;
}

img {
    display: inline-block;
}

.grey-text {
    color: $grey-color;
}

.cart {
    font-size: 20px;
}